import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { myFetcher } from './fetchAction';

const HackerNewsComponent = () => {

	/// Variables
	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);

	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);
	const baseURL = useSelector(state => state.app.appsList[currentApp].baseURL);
	const searchTerm = useSelector(state => state.app.appsList[currentApp].searchTerm);
	//let URL
	//if(searchTerm){
		//URL = `${baseURL}${searchTerm}`
	//} else {
		//URL = `${baseURL}_by_date?tags=story&hitsPerPage=50`
	//}
	const URL = (searchTerm) ? `${baseURL}${searchTerm}` : `${baseURL}_by_date?tags=story&hitsPerPage=50`

	const isOnline = (!navigator.onLine) ? false : true;
	//let URL = /*'http://localhost:2000/HackerNewsApi.php'; //*/(isOnline) ? `${baseURL}${searchTerm}` : 'http://localhost:2000/HackerNewsApi.php'
	//const URL = `${baseURL}${searchTerm}`


/*
	///===== Exclude unworkables =================

	/// No search term
	//React.useEffect(() => {
		//if(!searchTerm && !isOnline){
			///* TODO: maybe put in here some random selection..* /
			////dispatch({ type: 'TOGGLE_SEARCHBAR' });
			//console.log('branch 1');
		//}
		//if(!searchTerm && isOnline){
			///* TODO: maybe put in here some random selection..* /
			////dispatch({ type: 'TOGGLE_SEARCHBAR' });
			//console.log('branch 2');
		//}
		//if(searchTerm && ! isOnline){
			///* TODO: maybe put in here some random selection..* /
			////dispatch({ type: 'TOGGLE_SEARCHBAR' });
			//console.log('branch 3',  `${baseURL}${searchTerm}`);
		//}
		//if(searchTerm && isOnline){
			///* TODO: maybe put in here some random selection..* /
			////dispatch({ type: 'TOGGLE_SEARCHBAR' });
			//// http://hn.algolia.com/api/v1/search?tags=front_page
			//// https://hn.algolia.com/api/v1/search?query=tags=front_page
			//console.log('branch 4',  `${baseURL}${searchTerm}`);
		//}
  //}, [ baseURL, isOnline, dispatch, searchTerm]);

*/


	/// No internet
	if(!isOnline){
		return ( <div id='hackerApi' className=''>No internet connection.</div>)
	}

	if(!searchTerm && isOnline){
		// This case will never happen as, if there isn't one we are setting the search term to 'front page'
	}




	///===== Proceed with request ================

	/// Fetching data.
	if( ! hasData ){
		myFetcher(URL, dispatch)
		return (<div id='hackerApi' >Loading...</div>)
	}

	/// Request returned error.
	if( hasData === 'Error' ){
		/** TODO: in here - load dummy data? */
		return (<div id='hackerApi' >Error loading content.</div>)
	}

	/// Data OK - populate component content.
	if( hasData !== 'Error'){

		/// Sort items array by date.
		const sortedItems =  hasData.hits.sort((a, b) => parseInt(b.created_at) - parseInt(a.created_at));

		/// Iterate over array.
		const itemsList = sortedItems.map((item, i) => {

			/// Format the date
			const d = new Date(item.created_at);
			const formattedDate = d.toUTCString().substring(0, d.toUTCString().length -4);

			/// Return an div component with the n'th content.
			return (
					<div key={i}  className='hackerApi-box'>
						<a href={item.url} target="_blank" rel="noopener noreferrer">
							<div className='title'>{item.title}</div>
							<div className='date'>{formattedDate}</div>
							<div className='author'>Author: {item.author}</div>
							<div className='url'>{item.url}</div>
						</a>
					</div>
				)
		});

	return (<div id='hackerApi' >{ itemsList }</div> )
	}

}

export default HackerNewsComponent
